#scoreboard {
    width: 310px;
    text-align: center;
    margin-left: 68%;
}

p {
    color: #fff;
    font-size: 20px;
    padding: 3%; 
}

/* adjust mobile responsiveness */
@media only screen and (min-width: 768px) {
    #scoreboard {
        margin-left: 60%;
        width: 320px;
    } 
}

@media only screen and (max-width: 640px) {
    #scoreboard {
        margin-left: 25%;
    } 
    p {
        font-size: 16px;
    }
}

@media only screen and (max-width: 414px) {
    #scoreboard {
        margin-left: 0%;
    } 
    p {
        font-size: 16px;
    }
}
.navbar {
    background-color: #FF0971;
    border: none;
}

.navbar-brand {
    font-size: 36px;
}

.navbar-default .navbar-brand {
    color: #fff;
}

/* adjust mobile responsiveness */
/* @media only screen and (max-width: 411px) {
    
    }
} */
#instructionsDisplay {
    width: 70%
}

.jumbotron {
    background-color: black;
    color: gray;
    /* background-color: rgb(223, 222, 222); */
    padding: 5px;
    margin-top: 8%;
    border: .5px solid #FF0971;
}

.jumbotron>hr {
    border-top-color: #FF0971;
}


/* adjust mobile responsiveness */
@media only screen and (max-width: 812px) {
    .jumbotron {
        margin-top: 15%;
    }
}

@media only screen and (max-width: 640px) {
    #instructionsDisplay {
        width: 95%;
    }
    .jumbotron {
        margin-top: 25%;
    }
}

@media only screen and (max-width: 414px) {
    .jumbotron {
        margin-top: 40%;
    }
}


#display {
    width: 70%;
}

img {
    width: 210px;
    height: 225px;
}

button {
    margin: 9px;
    padding: 0;
    border: 5px solid #FF0971;
}

button:focus {
    outline: none;
}

/* adjust mobile responsiveness */
@media only screen and (max-width: 812px) {
    #display {
        width: 95%;
    }
}

